.product-item {
    border: 2px solid hsl(0, 42%, 45%);
    border-radius: 8px;
    padding: 10px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s;
    height: 450px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.product-image {
    max-width: 100%;
    max-height: 200px;
    object-fit: cover;
    border-radius: 4px;
    margin-bottom: 10px;
}

/* Success message */
.success-message {
    color: green;
    font-size: 1.2em;
    margin: 10px 0;
    padding: 10px;
    border: 1px solid green;
    border-radius: 5px;
    background-color: #e0f7e0;
}

/* Go to Cart button */
.go-to-cart-btn {
    display: block;
    margin: 20px 0;
    padding: 12px 20px;
    background-color: #28a745;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
    font-size: 1rem;
    font-weight: bold;
}

.go-to-cart-btn:hover {
    background-color: #218838;
}
